const Image = ({path, lowRes, highRes, description, author, year}) => {
	return ( 
		<a href={path + lowRes} target="_blank" rel="noopener noreferrer">
			<img src={path + highRes}
				alt={description}
				title={"\u00A9 " + author + year}
				className="caption"
			/>
		</a>
	 );
}
 
export default Image;